<template>
  <div>
    <!--  -->
    <video-player class="video-player-box" ref="videoPlayer" :options="playerOptions" :playsinline="true" customEventName="customstatechangedeventname" @play="onPlayerPlay($event)" @pause="onPlayerPause($event)" @ended="onPlayerEnded($event)" @statechanged="playerStateChanged($event)" @ready="playerReadied">
    </video-player>
    <!--  -->

    <!--  -->
    <div class="p-15 flex mb-10">
      <div class="course-info">
        <h2 class="fz-17 c3 mb-5"></h2>
        <div class="fz-12 c9 mb-5">14</div>
        <div>
          <span class="fz-15 text-price">99.00 </span>
          <span class="fz-12 c9 text-line">199.00</span>
        </div>
      </div>
      <div class="course-share flex flex-align-start">
        <div class="flex flex-column flex-jus flex-align-center">
          <img src="/img/icon-wallet.png" alt="">
          <div class="fz-11 c9"></div>
        </div>
        <div class="award-tips">46</div>
      </div>
    </div>
    <!--  -->
    <div class="bar-10"></div>
    <!--  -->
    <van-tabs v-model="active" title-active-color="#8DB9DF" title-inactive-color="#999999" color="#8DB9DF">
      <van-tab title="">

      </van-tab>
      <van-tab title="">
        <ul class="catalogue-list">
          <li class="catalogue-item" v-for="item in catalogue" :key="item.id" :data-id="item.id">
            <div class="thumb" :style="{backgroundImage:'url('+item.thumb+')'}">
              <div class="duration">{{item.duration}}</div>
            </div>
            <div class="fz-14 tilte" :class="item.id== current?'text-primary':'c3'">{{item.title}}</div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
    <!--  -->

    <!-- footer -->
    <div class="footer-bar plr-15 flex flex-align-center">
      <router-link to="/">
        <div class="back-home">
          <img src="/img/icon-home.png" alt="">
          <div class="fz-11 c9"></div>
        </div>
      </router-link>
      <div class="btn-youya" @click="buying"></div>
    </div>
    <!-- footer -->
  </div>
</template>

<script>
  // require styles
  import 'video.js/dist/video-js.css'
  import {
    videoPlayer
  } from 'vue-video-player'
  var Mock = require("mockjs");
  export default {
    components: {
      videoPlayer
    },
    props: {},
    data() {
      return {
        active: 1,
        current: 3,
        catalogue: [],
        playerOptions: {
          // videojs options
          muted: true,
          language: 'zh-CN',
          playbackRates: [0.5, 1.0, 1.5, 2.0],
          sources: [{
            type: "video/mp4",
            src: "/video/1.mp4"
          }],
          width: document.documentElement.clientWidth,
          poster: "/video/1.jpg",

        }
      };
    },
    watch: {},
    computed: {
      player() {
        return this.$refs.videoPlayer.player
      }
    },
    methods: {
      // listen event
      onPlayerPlay(player) {
        console.log('player play!', player)
      },
      onPlayerPause(player) {
        console.log('player pause!', player)
      },
      // ...player event

      // or listen state event
      playerStateChanged(playerCurrentState) {
        // console.log('player current update state', playerCurrentState)
      },

      // player is ready
      playerReadied(player) {
        console.log('the player is readied', player)
        // you can use it to do something...
        // player.[methods]
      },
      onPlayerEnded(playerEnd) {
        console.log('the player is ended', playerEnd)
      },
      // 
      buying(){
        this.$router.push({
          path: `/create_order/1`
        })
      }
    },
    created() {},
    mounted() {
      this.catalogue = Mock.mock({
        "list|8-10": [{
          "id|+1": 1,
          "thumb": Mock.Random.image('180x100', '#D8C7AF', 'video'),
          "title": Mock.Random.cparagraph(1,2),
          "duration": Mock.Random.time('H:m:s')
        }]
      }).list;
    }
  };
</script>
<style lang="less">
  .video-js .vjs-big-play-button {
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    line-height: 60px;
    border-radius: 50%;
    border: none;
    background: rgba(216, 216, 216, 0.6);
    opacity: 0.8;
    transform: translate(-50%, -50%);
  }

  .course-info {
    flex: 1;
  }

  .course-share {
    position: relative;
    width: 84px;
    padding-top: 10px;

    img {
      width: 24px;
      height: 24px;
    }

    .award-tips {
      position: absolute;
      top: 0;
      right: -5px;
      padding: 2px 4px;
      font-size: 10px;
      color: #fff;
      background: linear-gradient(141deg, rgba(252, 186, 133, 1) 0%, rgba(255, 169, 117, 1) 100%);
      border-radius: 11px 11px 11px 0;
    }
  }

  .footer-bar {
    box-shadow: 0px 0px 5px 0px rgba(238, 238, 238, 1);
  }

  .btn-youya {
    width: 260px;
    height: 40px;
    line-height: 40px;
    background: linear-gradient(143deg, rgba(157, 195, 230, 1) 0%, rgba(131, 179, 219, 1) 100%);
    border-radius: 20px;
    color: #fff;
    font-size: 15px;
    text-align: center;
    box-shadow: 1px 5px 6px rgba(131, 179, 219, .2);
  }

  .back-home {
    width: 90px;
    text-align: center;

    img {
      display: inline-block;
      width: 24px;
      height: 24px;
    }
  }
  .catalogue-list{
    padding: 15px;
    .catalogue-item{
      display: flex;
      margin-bottom: 15px;
      align-items: center;
      .thumb{
        position: relative;
        width: 90px;
        height: 50px;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        border-radius: 5px;
        .duration{
          position: absolute;
          right: 7px;
          bottom: 2px;
          font-size: 11px;
          color:#fff;
        }
      }
      .tilte{
        margin-left: 10px;
        flex: 1;
      }
    }
  }
</style>